*, *:before, *:after {
	box-sizing: border-box;
}

html {
	height: 100%;
	font-family: 'IBM Plex Sans',
		-apple-system,
		BlinkMacSystemFont,
		"Segoe UI",
		"Roboto",
		"Oxygen",
		"Ubuntu",
		"Cantarell",
		"Fira Sans",
		"Droid Sans",
		"Helvetica Neue",
		sans-serif;
	font-size: 100%;
	line-height: 1.5;
}

body {
	min-height: 100%;
	margin: 0;
	padding: 5px;
	color: #36464e;
}

button, input, textarea {
	font: inherit;
}

p {
	margin: 0 0 1.5rem;
}


.header {
	border-bottom: 1px solid #E4EAED;
	margin-bottom: 3rem;
}

.header-inner {
	width: 96%;
	max-width: 1366px;
	height: 80px;
	margin: 0 auto;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.header-cta-link {
	display: block;
	color: inherit;
	text-decoration: none;
	padding: 1em;
	background-color: #17FFFF;
}


.section-inner {
	width: 96%;
	max-width: 1366px;
	margin: 0 auto;
}

.hero {
	text-align: center;
}

.hero-title {
	font-size: 2.625em;
	font-weight: 300;
	line-height: 1.19;
	margin: 0 auto 3rem;
	max-width: 860px;
}

.hero-cta {
	display: inline-block;
	color: inherit;
	text-decoration: none;
	padding: 1em;
	background-color: #17FFFF;
	margin: 0 auto 4.5rem;
}

.hero-cta-icon {
	display: inline-block;
	margin-left: 1em;
}


@media (max-width: 46em) {
	.hero-title {
		font-size: 2.2em;
	}
}


@media (max-width: 28em) {
	.hero-title {
		font-size: 1.6em;
	}
}

.services {
	background-color: #FAFAFB;
	padding: 3rem 0;
}

.services-title,
.contact-title {
	text-transform: uppercase;
	font-size: 0.875em;
	line-height: 1.42;
	text-align: center;
	color: #5F7C8A;
	margin-bottom: 1.5rem;
}

.services-list {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-column-gap: 1.5rem;

	list-style: none;
	margin: 0;
	padding: 0;
}

.service {
	background-color: #fff;
	padding: 1.5rem 1rem 0;
	font-size: 0.875em;
	line-height: 1.42;
	height: min-content;
}

.service:focus,
.service:hover {
	outline: #17FFFF solid 2px;
}

.service-icon {
	margin-bottom: 1rem;
}

.service-title {
	margin: 0 0 1rem;
	line-height: 1.28;
}

.service-description {
	color: #5F7C8A;
}

.service-details {
	display: none;
	list-style: none;
	margin-left: 0;
	padding: 0;
}

.service-details-item:before {
	content: '- ';
}

.service:focus .service-details {
	display: block;
}

.service-actions {
	text-align: right;
}

.service-actions-expand {
	display: inline-block;
	width: 48px;
	height: 48px;
	background-repeat: no-repeat;
	background-position: center;
	background-image: url('/assets/img/chevron-down.svg');
}

.service:focus .service-actions-expand {
	background-image: url('/assets/img/chevron-up.svg');
}

@media (max-width: 52em) {
	.services-list {
		display: block;
		max-width: 620px;
		margin: 0 auto;
	}

	.service {
		margin-bottom: 1.5rem;
	}
}


.contact {
	padding: 3rem 0;
}

.contact-inner {
	width: 96%;
	max-width: 600px;
	margin: 0 auto;
}

.contact-title {
	margin-bottom: 3rem;
}

.contact-description {
	text-align: center;
	color: #5F7C8A;
	margin-bottom: 3rem;
}

.contact-info {
	text-align: center;
	list-style: none;
	padding: 0;
	margin: 0 0 3rem;
}

.contact-info-item {
	margin-bottom: 1.5rem;
}

.contact-info-item-link {
	color: inherit;
	text-decoration: none;
	font-weight: bold;
	border-bottom: 2px solid #17FFFF;
	padding-bottom: 2px;
}

.form-topRow {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-column-gap: 1.5rem;
}

.form-group {
	margin-bottom: 1.5rem;
}

.form-label {
	display: block;
	font-size: .75em;
	margin-bottom: .5rem;
}

.form-input,
.form-textarea {
	width: 100%;
	background-color: #F6F7F8;
	padding: 1em;
	border: 0;
	border-bottom: 1px solid #5F7C8A;
}

.form-input:focus,
.form-textarea:focus {
	outline: #17FFFF solid 2px;
}

.form-buttonContainer {
	text-align: center;
}

.form-button {
	padding: 1em;
	background-color: #17FFFF;
	margin: 0 auto 3rem;
	border: 0;
}

@media (max-width: 32em) {
	.form-topRow {
		display: block;
	}
}


.footer-copyright {
	font-size: .75em;
	text-align: center;
	font-style: italic;
	color: #5F7C8A;
}
